@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,600;1,100;1,200;1,400;1,500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
}

.ui.cards {
  margin-top: 4rem !important;
  justify-content: center;
}
.temp:first-child {
  font-size: 64px;
}
.main {
  padding: 2rem 0 !important;
}
.speed,
.hum {
  font-size: 30px;
}

.refresh {
  padding: 1rem 2rem;
  margin-top: 1rem;
  border: none;
  border-radius: 70px;
  background-color: rgb(68, 31, 31);
  color: white;
  font-weight: 600;
  letter-spacing: 1px;
  font-size: 1.2rem;

  font-family: "Montserrat", sans-serif;
}

.app {
  height: 100vh;
  background-color: rgb(97, 92, 92);
  padding: 7rem 0;
  text-align: center;
}

